
.Blogs .blog-title {
  font-size: 30px;
  font-weight: bold;
}
.Blogs .blog-body {
  box-sizing: border-box;
  padding: 10px;
  text-align: justify;
}
.Blogs .update, .Blogs .delete {
  font-size: 18px;
  font-weight: 600;
}
.Blogs .update:hover, .Blogs .delete:hover {
  color: red;
}



