
.Welcome {
  overflow: hidden;
}
.Welcome .Welcome-sort, .Welcome .Welcome-title, .Welcome .readAll {
    color: #222;
}
.Welcome .Welcome-sort:hover, .Welcome .Welcome-title:hover, .Welcome .readAll:hover {
      color: #000;
      text-decoration: underline;
}
.Welcome-content {
  margin: 0 auto;
  width: 950px;
  background-color: #fff;
}
.roughContent {
  font-size: 14px;
  margin: 10px 0;
  line-height: 20px;
}
.recentActive h1 {
  font-size: 24px;
  color: #999;
}
.recentActive h2 {
  font-size: 25px;
  font-weight: 700;
}
.recentActive data {
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  color: #828282;
  font-weight: 600;
}
.recentActive .readAll {
  float: right;
  height: 30px;
  font-weight: 700;
  font-size: 14px;
  line-height: 30px;
}
.recentActive ul {
  box-sizing: border-box;
  margin-top: 20px;
}
.recentActive li {
  word-break: break-all;
  margin-top: 10px;
}



